<script>
import lottie from 'lottie-web'
import tabJSON from '@/assets/json/tab.json' // 引入json文件


export default {
  name: "chart-base",
  props: {
    data:{
      type:Object,
      default:()=>{
        return {}
      }
    }
  },
  data() {
    return {
      show:false,
      instance: null,
      ready: false,
      options:{
　　　　　　autoplay: true,
　　　　　　loop: true
      },
      bgCircleTop:0,
      carouselTimer:null,
      carouselPropList:[
        {title:'NB客户接入',prop:'customerNum'},
        {title:'NB表具开户',prop:'meterOpenNum'},
        {title:'NB表具用气',prop:'gasAmount'},
      ],
      areaList:['northwest','southwest','southChina','centralChina','eastChina','northChina','northeast'],
      carouselIndex:0,
      showDetailIndex:0,
      carouselCur:{title:'NB客户接入',prop:'customerNum'},
      generalIntervalTime:3000,
      detailIntervalTime:5000,
      areaDetailInfo:{},
      areaCur:'',
      areaImg:null,
      showDetailInfo:false,
      chinaMapHeight:'',
  }
  },
  mounted() {
      //开启定时器
      var ruelsLottie1 =  lottie.loadAnimation({
              animationData:tabJSON,
              loop:true,
              autoplay:true,
              renderer:'svg',
              container: this.$refs.areaContainer, // 容器 //渲染方式，有"html"、"canvas"和"svg"三种
        });
        ruelsLottie1.play()
        setTimeout(()=>{
          this.chinaMapHeight = this.$refs.chinaMap.offsetHeight;
          this.showGeneralInfoTimer()
          this.showDetailInfoTimer();
        },3000)
  },
    methods:{
      showGeneralInfoTimer(){
          this.generalTimer = setInterval(()=>{
            if(this.carouselIndex>=2){
              this.carouselIndex = 0
            }else{
              this.carouselIndex ++
            }
            this.carouselCur = this.carouselPropList[this.carouselIndex]
          },this.generalIntervalTime)
      },
      showDetailInfoTimer(){
          this.detailTimer = setInterval(()=>{
            if(this.showDetailIndex>=6){
              this.showDetailIndex = 0
            }else{
              this.showDetailIndex ++
            }
            var area = this.areaList[this.showDetailIndex]
            this.areaCur = area;
            this.showDetailInfo = false;
            this.areaDetailInfo =  this.data[area]
            this.$nextTick(()=>{  //带有背景的文字，文字改变了，背景不改变，v-if触发重新渲染css
              this.showDetailInfo = true;
            })
          },this.detailIntervalTime)
      },
      stopTimer(timer){
        clearInterval(this[timer]);
        this[timer]=null;
      },
      hoverArea(area){
        if(this.areaCur == area){
          return;
        }else{
          this.stopTimer('detailTimer');
          this.showDetailIndex = this.areaList.findIndex(item=>{ return item == area }) ;
          var area = this.areaList[this.showDetailIndex]
          this.areaCur = area;
          this.showDetailInfo = false;
          this.areaDetailInfo =  this.data[area]
          this.$nextTick(()=>{  //带有背景的文字，文字改变了，背景不改变，v-if触发重新渲染css
            this.showDetailInfo = true;
          })
          this.showDetailInfoTimer();
        }
      },
      unHoverArea(){
        this.areaCur = '';
        this.showDetailInfo = false;
        this.showGeneralInfoTimer()
      }
    },
    beforeDestroyed(){
       this.stopTimer('generalTimer');
       this.stopTimer('detailTimer');
    }
}
</script>

<style lang="scss" scoped>
