          font-size:24px;
          color:rgba(33,229,255,1);
          background:linear-gradient(360deg, rgba(255,255,255,1) 0%, rgba(33,229,255,1) 100%);
          -webkit-background-clip:text;
          -webkit-text-fill-color:transparent;
          box-sizing:border-box;
        }
        .divide-line{
           padding: 0 10px;
        }
        .info-item{
          display: flex;
          justify-content: space-around;
          .num{
            color:rgba(30,219,248,1);
            display: inline-block;
            padding-right:7px;
          }
          .fs24{
            font-size:24px;
          }
          .data{
            width:110px;
          }
          .ratio-text{
            position:absolute;
            top: 0;
            left: 0;
            width: 66px;
            height:66px;
            text-align:center;
            font-size:10px;
            font-family:MicrosoftYaHeiLight;
            color:rgba(255,255,255,1);
            line-height:14px;
            display:flex;
            justify-content: center;
            align-items: center;
            flex-direction:column;
            .ratio{
              color:rgba(33,229,255,1);
              line-height:24px;
              font-size:18px;
            }
          }
        }
      }
    }
  }
  .area-info{
    position:absolute;
    top:0;
    right:0;
    left:0;
    bottom:0;
  }
  .area-outline{
    position:absolute;
    top:0;
    right:0;
    left:0;
    bottom:0;
  }

}

</style>
